import Vue from "vue";
import Router from "vue-router";

Vue.use(Router);

export default new Router({
  // mode: "history",
  scrollBehavior: () => ({ y: 0 }),
  routes: [
    {
      path: "/",
      component: () => import("@/views/home/index.vue"),
      hidden: true,
      redirect: "/main",
      children: [
        {
          path: "main",
          component: () => import("@/views/home/Main.vue"),
          hidden: true
        },
        {
          path: "about",
          component: () => import("@/views/home/About.vue"),
          hidden: true
        }
      ]
    }
  ]
});
